* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
body {
	font-family: 'Nanum Gothic', sans-serif;
}
code {
	background-color: rgb(41, 45, 62);
	box-sizing: border-box;
	color: rgb(255 140 0);
	display: block;
	float: left;
	font-family: SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono',
		'Courier New', monospace;
	font-size: 13.5px;
	height: 220px;
	line-height: 19.575px;
	min-width: 100%;
	overflow-wrap: break-word;
	text-rendering: optimizelegibility;
	-webkit-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	text-size-adjust: 100%;
	white-space: pre;
	width: 914px;
	-webkit-font-smoothing: antialiased;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	padding: 20px;
	margin-top: 15px;
	margin-bottom: 15px;
}
.home,
.categories,
.characters,
.shows {
	display: -webkit-flex;
	display: flex;
	height: 90vh;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	font-size: 1.2em;
	padding: 20px;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
.sub {
	background: #ff8000;
	font-weight: bold;
	padding: 2px 5px;
}
.profilePage {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 20px;
	-webkit-align-items: center;
	        align-items: center;
}

/* Create three equal columns that floats next to each other */
.column {
	/* display: grid; */
	float: left;
	width: 100%;
	padding: 10px;
	height: auto;
	border: 1px solid orange;
	border-radius: 5px 5px;
	margin-top: 5px;
	min-height: 400px;
}
.row {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(2, 50%);
	justify-items: center;
	-webkit-align-items: baseline;
	        align-items: baseline;
	grid-gap: 4px 5px;
}
/* Clear floats after the columns */
.row:after {
	content: '';
	display: table;
	clear: both;
}
ul {
	display: block;
	margin: 0 auto;
	position: relative;
	text-align: justify;
	list-style: inside;
}
.column > ul > li {
	border: 1px solid orange;
    padding: 8px 18px;
    margin: 10px 0px;
    border-radius: 5px;
}
/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
	.row {
		grid-template-columns: repeat(1, 100%);
	}
}
.button {
	background: orange;
	padding: 2px 6px;
	margin: 0 5px;
	border: 2px solid black;
	font-weight: 700;
	border-radius: 5px;
	cursor: pointer;
}
.error {
	color: red;
	font-size: 0.8em;
}
form {
	display: grid;
}
form > * {
	margin-top: 3px;
	margin-bottom: 3px;
}
.character_image {
	width: 50px;
	height: 50px;
	margin: 2px 5px;
}

.navbar {
	background-color: #060b26;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: center;
	        align-items: center;
}
.menu-bars {
	margin-left: 2rem;
	font-size: 2rem;
	background: none;
}
.nav-menu {
	background-color: #060b26;
	width: 250px;
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	position: fixed;
	top: 0;
	left: -100%;
	transition: 850ms;
	z-index: 1;
}
.nav-menu.active {
	left: 0;
	transition: 350ms;
	z-index: 1;
}
.nav-text {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: center;
	        align-items: center;
	padding: 8px 0px 8px 16px;
	list-style: none;
	height: 60px;
}

.nav-text a {
	text-decoration: none;
	color: #f5f5f5;
	font-size: 18px;
	width: 95%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 0 16px;
	border-radius: 3px;
}
.nav-text a:hover {
	color: black;
	transition: 0.2s all;
	background-color: rgb(255 140 0);
}
.nav-menu-items {
	width: 100%;
}
.navbar-toggle {
	background-color: #060b26;
	width: 100%;
	height: 80px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: center;
	        align-items: center;
}
span {
	margin-left: 16px;
}
.profile {
	display: -webkit-inline-flex;
	display: inline-flex;
	width: 45%;
	height: inherit;
	padding: 7px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	grid-gap: 4px;
	gap: 4px;
	position: absolute;
	right: 5px;
	background: darkorange;
	color: black;
}
.profileImg {
	width: 55px;
	height: 55px;
	border-radius: 50px;
	border: 2px solid white;
}
.profileText {
	font-size: 1em;
}
.profileEmail {
	font-size: 1em;
}

.loggedIn {
	color: white;
	display: inline-block;
	right: 20px;
	margin-right: 0;
	position: absolute;
	background: #19a051;
	padding: 4px 12px;
	border-radius: 5px;
}
.loggedOut {
	color: white;
	display: inline-block;
	right: 20px;
	margin-right: 0;
	position: absolute;
	background: #ed6464;
	padding: 4px 12px;
	border-radius: 5px;
}
@media all and (max-width: 700px) {
	.profile {
		width: 72%;
	}
	.profileText {
		font-size: 0.7em;
	}
	.profileEmail {
		font-size: 0.6em;
	}
}

